  margin-left: auto;
  margin-right: auto;
  padding-left: .5rem;
  padding-right: .5rem; }

.ph14-section {
  margin: 0 0 3rem; }

.ph14-spacer {
  margin: 0 0 1rem; }

body {
  color: #555;
  font-family: "sans-serif";
  font-size: 17px;
  line-height: 1.4; }

h1, h2, h3, h4, h5, h6 {
  color: #222;
  margin: .6rem 0;
  line-height: 1.15; }

h1 {
  font-size: 2.5rem;
  margin: 1rem 0 2rem; }

h1 + h2 {
  margin-top: -1.4rem; }

h2 {
  font-size: 1.8rem; }

p {
  font-size: 1rem; }

a {
  text-underline-position: under;
  text-decoration-skip: objects; }
  a:link {
    color: #2890d2; }
  a:visited {
    color: #7c21cc; }
  a:hover, a:focus {
    text-decoration: none; }
  a:focus {
    background: #d0e7f6;
    outline: 2px solid #d0e7f6; }

code,
kbd,
samp {
  color: #d41c78; }

strong, b {
  color: #222; }

blockquote,
[role="note"] {
  border-left: 20px solid #ddd;
  padding: 1rem;
  margin: 2rem 0; }

q {
  display: inline-block;
  padding: .1rem .2rem;
  margin: .2rem;
  background: #f8f8f8;
  font-style: italic; }

table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1rem; }

th,
td {
  padding: .3rem; }

th {
  text-align: left;
  color: #222; }

thead tr + tr th {
  border-top: 1px solid #ddd; }

tbody tr:nth-child(odd) td {
  background: #f8f8f8; }

tbody tr:hover td {
  background: #eeeeee; }

tfoot {
  color: #888;
  font-size: .95rem;
  font-style: italic; }
  tfoot td {
    border-top: 1px dashed #ddd; }

form {
  margin-bottom: 1rem; }

fieldset {
  padding: 1rem 1rem .5rem;
  margin: 0 0 1.5rem;
  border: 1px dashed #ddd; }
  fieldset legend {
    font-style: italic; }

label {
  display: inline-block;
  margin-right: .5rem;
  margin-bottom: .2rem;
  color: #222;
  font-size: 1rem; }

input,
select,
textarea {
  margin-bottom: 1rem;
  box-shadow: 0 0 0 1px #ddd;
  outline: 0;
  border: 2px solid transparent;
  transition: box-shadow 0.1s linear, border-color 0.1s linear; }
  input:hover,
  select:hover,
  textarea:hover {
    box-shadow: 0 0 0 1px #bebebe; }
  input:focus,
  select:focus,
  textarea:focus {
    box-shadow: none;
    border-color: #2890d2; }

input[type=color],
input[type=date],
input[type=datetime-local],
input[type=email],
input[type=month],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=text],
input[type=time],
input[type=url],
input[type=week],
textarea {
  color: #555;
  background: #fff;
  padding: .4rem .6rem;
  line-height: 1.4; }
  input[type=color][disabled], input[type=color][disabled=disabled],
  input[type=date][disabled],
  input[type=date][disabled=disabled],
  input[type=datetime-local][disabled],
  input[type=datetime-local][disabled=disabled],
  input[type=email][disabled],
  input[type=email][disabled=disabled],
  input[type=month][disabled],
  input[type=month][disabled=disabled],
  input[type=number][disabled],
  input[type=number][disabled=disabled],
  input[type=password][disabled],
  input[type=password][disabled=disabled],
  input[type=search][disabled],
  input[type=search][disabled=disabled],
  input[type=tel][disabled],
  input[type=tel][disabled=disabled],
  input[type=text][disabled],
  input[type=text][disabled=disabled],
  input[type=time][disabled],
  input[type=time][disabled=disabled],
  input[type=url][disabled],
  input[type=url][disabled=disabled],
  input[type=week][disabled],
  input[type=week][disabled=disabled],
  textarea[disabled],
  textarea[disabled=disabled] {
    color: #888;
    background: #f8f8f8; }

input[type=checkbox],
input[type=radio] {
  margin: 0 .1rem 0 0;
  border: 0;
  box-shadow: none; }

select {
  padding: 0.5rem; }

input[type=submit],
input[type=button],
button,
[role=button] {
  appearance: none;
  padding: .5rem 1rem;
  line-height: 1.4;
  border: 0;
  outline: 0 !important;
  background: #2890d2;
  color: #fff !important;
  text-decoration: none !important;
  margin: 0 .2rem .2rem 0;
  box-shadow: 2px 2px #1e6d9f;
  border-radius: 3px; }
  input[type=submit]:hover,
  input[type=button]:hover,
  button:hover,
  [role=button]:hover {
    background: #3699d9; }
  input[type=submit]:focus, input[type=submit]:active,
  input[type=button]:focus,
  input[type=button]:active,
  button:focus,
  button:active,
  [role=button]:focus,
  [role=button]:active {
    background: #3e9eda;
    box-shadow: inset 2px 2px 0 1px #2584c1; }

ol,
ul {
  list-style: disc;
  list-style-position: outside;
  margin: 1.4rem 0 1.4rem 1.2rem;
  padding: 0; }

ol {
  list-style: decimal; }

nav ol,
nav ul {
  list-style: inherit; }

li {
  margin-bottom: .2rem;
  padding-left: .2rem;
  margin-left: 1rem; }
